  /*>>>>>>>> Feature area Common styling <<<<<<<<<*/



  /*~~~~Feature Area styling 1~~~~~*/ 

  .omga-01__feature-section {
    background: $l1-secondary-color;
    // color: #fff;
    position: relative;
    .shape-1{
      position: absolute;
      top: 0;
      right: 0px;
      
    }
    .shape-2{
      position: absolute;
      bottom: -10px;
      left: 0;
      width: 100%;
      img {
        width: 100%;
      }
    }
    
    .feature-content {
      .title {
        color: #fff;
        margin-bottom: 25px;
      }
      p {
        color: rgba(255, 255, 255, 0.7);
      }
      .content-btn {
        margin-top: 35px;
      }
    }
  }
  .feature-card--01 {
    padding: 25px 30px 12px;
    border-radius: 10px;
    background-color: #ffffff;
    transition: .4s;
    .card-icon {
      width: 69px;
      height: 69px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 28px;
      margin-bottom: 22px;
      &.red {
        background-color: rgba($l1-primary-color, 0.1);
        color: $l1-primary-color;
      }
      &.yellow {
        background-color: rgba(254, 220, 90, 0.1);
        color: $theme-yellow-color;
      }
      &.blue {
        background-color: rgba(84, 84, 212, 0.1);
        color: $l1-secondary-color;
      }
      &.ash {
        background-color: rgba(65, 62, 101, 0.1);
        color: $theme-ash-color;
      }
    }
    .card-text {
      .title {
        color: $l1-primary-heading;
        font-family: $l1-primary-font;
        font-size: 24px;
        font-weight: 500;
        letter-spacing: -0.75px;
        margin-bottom: 16px;
      }
      p {
        color: $l1-primary-text;
        font-family: $l1-primary-font;
        font-size: 16px;
        font-weight: 300;
        letter-spacing: -0.5px;
        line-height: 28px;
      }
    }
    &:hover {
      box-shadow: 0 52px 54px rgba(65, 62, 101, 0.3);
    }
  }

  /*~~~~Feature Area styling 2~~~~~*/ 

  .omga-02_ {
    &_feature-section {
      background: $l2-secondary-color;
      position: relative;
      
      .feature-content {
        .title {
          color: #fff;
          margin-bottom: 25px;
        }
        p {
          font-size: 21px;
          color: rgba(255, 255, 255, 0.7);
        }
        .content-btn {
          margin-top: 35px;
        }
      }
    }
  }
  
  .feature-widget--2 {
    border-radius: 10px;
    border: 1px solid #eae9f2;
    background-color: #ffffff;
    padding: 30px;
    min-width: 100%;
    width: 100%;
    display: flex;
    justify-content: space-between;
    @include tablet {
      width: 399px;
      min-width: 389px;
    }
    .widget-icon {
      flex-basis: auto;
      width: 69px;
      height: 69px;
      border-radius: 500px;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 30px;
      margin-right: 20px;
      &.red {
        color: $l2-primary-color;
        background-color: rgba(240, 64, 55, 0.1);
      }
      &.yellow {
        background-color: rgba(254, 220, 90, 0.1);
        color: $theme-yellow-color;
      }
      &.blue {
        background-color: rgba(84, 84, 212, 0.1);
        color: $l2-secondary-color;
      }
      &.ash {
        background-color: rgba(65, 62, 101, 0.1);
        color: $l2-secondary-color;
      }
      
    }
    .widget-text {
      flex-basis: calc(100% - 100px);
      .title {
        color: $theme-primary-heading;
        font-family: $theme-primary-font;
        font-size: 24px;
        font-weight: 700;
        letter-spacing: -0.75px;
        margin-bottom: 16px;
      }
      .sub-title {
        color: $theme-primary-text;
        font-family: $theme-primary-font;
        font-size: 16px;
        font-weight: 300;
        letter-spacing: -0.5px;
        line-height: 28px;
      }
    }
  }

  /*~~~~Feature Area styling 3~~~~~*/ 

  .omga-03__feature-section{
    .video-image{
      position: relative;
      img{
        box-shadow: 0 42px 54px rgba(0, 0, 0, 0.31);
        border-radius: 10px;
      }
      a{
        font-size: 22px;
        background-color: #5454D4;
        border-radius: 500px;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
        color: #fff;
        width: 64px;
        height: 64px;
        @include mobile-lg{
          width: 100px;
          height: 100px;
        }
        @include tablet{
          width: 124px;
          height: 124px;
        }
      }
    }
  }
  .feature-widget--3{
    text-align: center;
    padding-left: 25px;
    padding-right: 25px;
    @include large-desktops{
      padding-left: 35px;
      padding-right: 35px;
    }
    .widget-icon{
      min-width: 69px;
      max-width: 69px;
      min-height: 69px;
      max-height: 69px;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      border-radius: 500px;
      margin-bottom: 17px;
      font-size: 26px;
      &.bg{
        &-red{
          background-color: rgba(240, 64, 55, 0.1);
          color: #f04037;
        }
        &-blue{
          background-color: rgba(84, 84, 212, 0.1);
          color: #5454d4;
        }
        &-yellow{
          background-color: rgba(254, 220, 90, 0.1);
          color: #fedc5a;
        }
      }
    }
    .widget-text{
      .title{
        display: block;
        // color: #FFFFFF;
        font-size: 24px;
        font-weight: 700;
        letter-spacing: -0.75px;
        margin-bottom: 14px;
      }
      p{
        // color: rgba(255, 255, 255, 0.7);
        // font-family: "Circular Std";
        font-size: 16px;
        font-weight: 300;
        letter-spacing: -0.5px;
        line-height: 28px;
        &:last-child{
          margin-bottom: 0;
        }
      }
    }
  }


