/*~~~~Feature Area styling 7~~~~~*/ 
.feature-widget--7 {
  display: flex;
  @include desktops {
      margin-bottom: 0;
  }
  .widget__icon {
      position: relative;
      min-width: 62px;
      min-height: 62px;
      max-width: 62px;
      max-height: 62px;
      justify-content: center;
      display: flex;
      align-items: center;
      margin-bottom: 35px;
      margin-right: 25px;
      .inner-circle {
          width: 14px;
          height: 14px;
          border-radius: 50%;
      }
      
      .outer-circle {
          width: 62px;
          height: 62px;
          border-radius: 50%;
          position: absolute;
      }
  }

  .widget__body {
      text-align: left;
      p {
        color: #696871;
        font-size: 16px;
        font-weight: 300;
        letter-spacing: -0.5px;
        line-height: 28px;
      }
  }
  .widget__heading {
      color: #19191b;
      font-weight: 700;
      margin-bottom: 15px;
      font-size: 24px;
  }
}

