    }
  }
  .nice-select {
   &:after{
    position: relative;
    content: "\ea05";
    font-family:'Grayic';
    font-size: 35px;
    top: 0;
    transform: rotate(0);
    border: none;
    width: auto;
    height: auto;
    transform-origin: center;
   }
   &.open{
    &:after{
      transform: rotate(180deg);
    }
   }
  }
  
  /* For IE10 */
  select::-ms-expand {
    display: none;
  }
  
  .nice-select {
    height: 100%;
