      width: 100%;
      margin-top: 0;
      li{
        line-height: 45px;
        list-style: none;
        min-height: 45px;
      }
    }
  }

  .hero-btn {
    margin-top: 20px;
    button {
      width: 100%;
      border-radius: 10px;
      overflow: hidden;
    }
  }
}



